<template>
  <div class="travel" :class="{'bg': background}">
    <div class="container p-m-0">
      <div class="travel-bg mb-10" :class="{'reverse': reverse}">
        <div class="content">
          <slot></slot>
          <p>
            {{ travelData.day_summary }}
          </p>
        </div>
      </div>
      <div class="travel-inner">
        <div class="travel-title" :class="{'only-one': travelData.components.length === 1}">
          <ul class="p-2">
            <template v-for="(item, index) in travelData.components" :key="index">
              <li
                v-if="index < 4 || isMore === true"
                :class="{ active: tempTravelData.name === item.name }"
                @click="tempTravelData = item"
              >
                <div class="travel-images">
                  <img :src="item.imgs[0].url" :alt="item.name" />
                </div>
                <div class="desc">
                  <h3>{{ item.name }}</h3>
                  <p>
                    {{ trim_description(item.description) }}
                  </p>
                  <router-link :to="{ name: 'detail', params: { data: JSON.stringify(item) } }">
                    <span>查看更多</span>
                    <svg
                      width="8"
                      height="5"
                      viewBox="0 0 8 5"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M7.84169 1.0774L4.29551 4.70106C4.2533 4.74407 4.20756 4.77446 4.15831 4.79224C4.10906 4.8103 4.05629 4.81934 4 4.81934C3.94371 4.81934 3.89094 4.8103 3.84169 4.79224C3.79244 4.77446 3.7467 4.74407 3.70449 4.70106L0.147757 1.0774C0.0492524 0.977042 0 0.851594 0 0.701056C0 0.550519 0.0527704 0.421487 0.158311 0.31396C0.263852 0.206433 0.386983 0.152669 0.527704 0.152669C0.668426 0.152669 0.791557 0.206433 0.897098 0.31396L4 3.47525L7.1029 0.31396C7.20141 0.213601 7.32271 0.163422 7.46681 0.163422C7.61119 0.163422 7.73615 0.217185 7.84169 0.324712C7.94723 0.432239 8 0.557687 8 0.701056C8 0.844426 7.94723 0.969874 7.84169 1.0774Z"
                      />
                    </svg>
                  </router-link>
                </div>
              </li>
            </template>
            <li
              class="more"
              v-if="travelData.components.length > 4"
              @click="isMore = !isMore"
            >
              <a href="#">
                <span>{{ isMore ? "顯示較少" : "顯示全部" }}</span>
                <svg
                  :class="{ less: isMore }"
                  width="8"
                  height="5"
                  viewBox="0 0 8 5"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M7.84169 1.0774L4.29551 4.70106C4.2533 4.74407 4.20756 4.77446 4.15831 4.79224C4.10906 4.8103 4.05629 4.81934 4 4.81934C3.94371 4.81934 3.89094 4.8103 3.84169 4.79224C3.79244 4.77446 3.7467 4.74407 3.70449 4.70106L0.147757 1.0774C0.0492524 0.977042 0 0.851594 0 0.701056C0 0.550519 0.0527704 0.421487 0.158311 0.31396C0.263852 0.206433 0.386983 0.152669 0.527704 0.152669C0.668426 0.152669 0.791557 0.206433 0.897098 0.31396L4 3.47525L7.1029 0.31396C7.20141 0.213601 7.32271 0.163422 7.46681 0.163422C7.61119 0.163422 7.73615 0.217185 7.84169 0.324712C7.94723 0.432239 8 0.557687 8 0.701056C8 0.844426 7.94723 0.969874 7.84169 1.0774Z"
                  />
                </svg>
              </a>
            </li>
          </ul>
        </div>
        <div class="travel-content">
          <div class="inner p-6">
            <Swiper
              :modules="modules"
              :slides-per-view="1"
              :space-between="10"
              navigation
              loop
              :speed="1000"
              :autoplay="{ delay: 2000 }"
              :pagination="{ clickable: true }"
            >
              <SwiperSlide v-for="(item, index) in tempTravelData.imgs" :key="index">
                <img :src="item.url" :alt="item.description" />
              </SwiperSlide>
            </Swiper>
            <div class="desc">
              <h3>{{ tempTravelData.name }}</h3>
              <p>
                {{ tempTravelData.description }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Pagination, Autoplay, Navigation } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/css'
import 'swiper/css/pagination'

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  props: {
    travelData: {
      type: Object,
      required: true
    },
    background: {
      type: Boolean,
      default () {
        return false
      }
    },
    reverse: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      isMore: false,
      tempTravelData: this.travelData.components[0],
      modules: [Pagination, Autoplay, Navigation]
    }
  },
  methods: {
    trim_description (text) {
      if (typeof text === 'string' && text.length > 0) {
        return text.substring(0, 150) + '...'
      } else {
        return ''
      }
    }
  }
}
</script>
