    <ul :class="getRoadmapClass">
      <li v-for="(item, index) in travelRoadMap" class="roadmap-item" :key="item">
        <div class="circle" :class="roadmapSet[index]">
          <p
            :class="{
              last: index + 1 === travelRoadMap.length && travelRoadMap.length < 7,
              first: index === 0 && travelRoadMap.length < 7,
            }"
          >
            {{ item }}
          </p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    travelRoadMap: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      roadmapSet: [],
      direction: ['top', 'bottom'],
      length: ['s', 'l']
    }
  },
  computed: {
    getRoadmapClass () {
      if (this.travelRoadMap.length < 7) {
        return 'count-normal'
      } else {
        return `count${this.travelRoadMap.length}`
      }
    }
  },
  methods: {
    setRoadMap () {
      this.roadmapSet = []
      let flag = 1
      for (let i = 0; i < this.travelRoadMap.length; i++) {
        if (i === 0) {
          this.roadmapSet.push(`${this.direction[i]}-${this.length[i]}`)
        } else if (i % 2 === 1 && flag === 1) {
          flag = 0
          this.roadmapSet.push(`${this.direction[1]}-${this.length[1]}`)
        } else if (i % 2 === 1 && flag === 0) {
          this.roadmapSet.push(`${this.direction[1]}-${this.length[0]}`)
          flag = 1
        } else if (i % 2 === 0 && flag === 0) {
          this.roadmapSet.push(`${this.direction[0]}-${this.length[1]}`)
          flag = 0
        } else if (i % 2 === 0 && flag === 1) {
          flag = 1
          this.roadmapSet.push(`${this.direction[0]}-${this.length[0]}`)
        }
      }
    }
  },
  mounted () {
    this.setRoadMap()
  }
}
</script>

